import React, { useState } from 'react';
import{db} from '../files/firebase';
import{Card} from 'react-bootstrap';
import {toast} from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { useLocation } from 'react-router-dom';
import Studentoptions from './student_options';
toast.configure()

const Rejectedpermission =()=>{
      var location =useLocation();  
    
    const[students,setStudents] = useState([]);
        var rollno = location.state.studentrollno;
        var section = location.state.studentsection;
        var year = location.state.studentyear;
       
        db.collection("rejectedpermissions").where("rollno","==",rollno).where("section","==",section).where("year","==",year).get().then(snapshot => {
           const students = [];
           snapshot.forEach( doc => {
               const data = doc.data();
               students.push(data);
           })
          setStudents(students);
        }).catch(error => console.log(error))
    
    
   
    return <div>
        <Studentoptions studentrollno={location.state.studentrollno}studentname={location.state.studentname}studentsection={location.state.studentsection}studentdepartment={location.state.studentdepartment}studentyear={location.state.studentyear}/>
        <br/><br/><br/><br/>
    <Card className="rejected-card">
         <h2>Rejected Letter</h2>
        <br/>
    <Card.Body>   
        {
            students.map( student => {
               
                return(
                    <div>
                        <p>Date : {student.date}</p>
                        <p>Name : {student.name}</p>
                        <p>Rollno : {student.rollno}</p>
                        <p>Section : {student.section}</p>
                        <p>Year : {student.year}</p>
                        <p>Department : {student.department}</p>
                        <p>No of Days : {student.days}</p>
                        <p>Startdate : {student.startdate}</p>
                        <p>Enddate : {student.enddate}</p>
                        <p>Reason : {student.reason}</p>
                        <p id="hod-permission-rejected-status"><i className="fa fa-close"></i>&nbsp;&nbsp;&nbsp;{student.status}</p>
                        <br/><br/><br/>
                    </div>
                )
            })
        }
        
    </Card.Body>
    
    </Card>
    </div>
   
    }    

export default Rejectedpermission;
