import React from 'react';
import{Link} from 'react-router-dom';

const Hodoptions = ()=>{
    return <div>
       <div className="btn-group float-right" role="group" aria-label="Button group with nested dropdown"id="button-group">
  
  <div className="btn-group" role="group">
    <button id="btnGroupDrop1" type="button" className="btn btn-secondary dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
      Add
    </button>
    <div className="dropdown-menu" aria-labelledby="btnGroupDrop1">
      <Link className="dropdown-item"to="/addtutorpage" >Add Tutors</Link>
      <Link className="dropdown-item"to="/addteacherpage">Add Teachers</Link>
    </div>
  </div>

  <div className="btn-group" role="group">
    <button id="btnGroupDrop1" type="button" className="btn btn-secondary dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
      View
    </button>
    <div className="dropdown-menu" aria-labelledby="btnGroupDrop1">
      <Link className="dropdown-item"to="/Monthlyreport" >Monthly Reports</Link>
      <Link className="dropdown-item"to="/feedbackpage">feedback</Link>
    </div>
  </div>
 
  <div className="btn-group" role="group">
    <button id="btnGroupDrop1" type="button" className="btn btn-secondary dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
      Settings
    </button>
    <div className="dropdown-menu" aria-labelledby="btnGroupDrop1">
      <Link className="dropdown-item" to="/Logout">Logout</Link>
    </div>
    
  </div>
</div></div>
}

export default Hodoptions;