
import React from 'react';
import{Link} from 'react-router-dom';

class Teacheroptions extends React.Component{
 
  render(){
     
    return <div>
         <div className="btn-group float-right" role="group" aria-label="Button group with nested dropdown"id="teacher-options">
  
  <div className="btn-group" role="group">
    <button id="btnGroupDrop1" type="button" className="btn btn-secondary dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
      View
    </button>

    <div className="dropdown-menu" aria-labelledby="btnGroupDrop1">
      <Link className="dropdown-item" to={{pathname:"/Permissionimageretrieve"}}>Permissions</Link>
            <Link className="dropdown-item" to={{pathname:"/studentattendance"}}>Attendance</Link>
       <Link className="dropdown-item" to={{pathname:"/Logout"}}>Logout</Link>
    </div>
  </div>
  </div>
    </div>
  }
}
export default Teacheroptions;