import React from 'react';
import{db} from '../files/firebase';
import{Card,Form} from 'react-bootstrap';
import Buttongroup from './button_group';
import {toast} from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
toast.configure()

class Viewattendance extends React.Component{
    constructor(){
        super();
        this.state={
            section:'',
            year:'',
        }
    }
    state={
        students:null,
    };
     handleSection(sectionvalue){
        this.setState({
            section:sectionvalue
        });
    }
     handleYear(yearvalue){
        this.setState({
            year:yearvalue
        });
       
        var currentDate = new Date().toISOString().slice(0,10);
         
          alert(this.state.section)
        alert(yearvalue)
        alert(currentDate)
        db.collection("studentabsentreport").where("date","==",currentDate).where("section","==",this.state.section).where("year","==",yearvalue).get().then((snapshot)=>{
            const students = [];
            snapshot.forEach((doc)=>{
                const data = doc.data();
                students.push(data);
            })
            this.setState({
                students:students,
            })
        }).catch((error)=>{
                console.log(error);
            })
    }
    render(){
       
        return<>
        <br/><br/><br/><br/>
         <Form.Group controlId="formBasicSection">
     <Form.Label>Pick Student Section</Form.Label>
     <br/>
     <select className="section"value={this.state.section}onChange={(e)=>this.handleSection(e.target.value)}>
         <option>Pick Section</option>
  <option value="CSE-A">CSE-A</option>
  <option value="CSE-B">CSE-B</option>
  <option value="CSE-C">CSE-C</option>
</select>
 </Form.Group>

 <Form.Group controlId="formBasicYear">
     <Form.Label>Pick Student Year</Form.Label>
     <br/>
     <select className="section"value={this.state.year}onChange={(e)=>this.handleYear(e.target.value)}>
         <option>Pick Year</option>
  <option value="I-year">I-year</option>
  <option value="II-year">II-year</option>
  <option value="III-year">III-year</option>
  <option value="IV-year">IV-year</option>
</select>
 </Form.Group>
<br/>
           <Buttongroup />
        <Card className="permission-card">
            <h2>Todays Absentee Report</h2>
            <br/>
            <Card.Body>
                {
                    this.state.students && this.state.students.map((student)=>{
                         const messageFunction = ()=>{
          var message = prompt("Enter message");
          var date = student.date;
          var name = student.name;
          var rollno = student.rollno;
          var section = student.section;
          var year = student.year;
          db.collection("studentmessages").add({
              "date" : date,
              "name" : name,
              "rollno" : rollno,
              "section" : section,
              "year" : year,
              "message" : message
          }).then(()=>{
            toast("The message has been sent to student....");
          }).catch((error)=>{
              console.log(error);
          })
        }
                        return(
                            <div>
                                <p>
                                Date : {student.date}
                                </p>
                                <p>Name : {student.name}</p>
                                <p>Rollno : {student.rollno}</p>
                                <p>Section : {student.section}</p>
                                <p>Year : {student.year}</p>
                                <p><i className="fa fa-envelope"id="message-icon"onClick={messageFunction}></i></p>
                                <br/><br/>
                            </div>
                        );
                    })
                }
            </Card.Body>
        </Card>
    </>
    }
    
}
export default Viewattendance;