import React from 'react';
import{Link} from 'react-router-dom';

const Buttongroup = ()=>{
    return <div>
       <div className="btn-group float-right" role="group" aria-label="Button group with nested dropdown"id="incharge-options">
  
  <div className="btn-group" role="group">
    <button id="btnGroupDrop1" type="button" className="btn btn-secondary dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
      Add
    </button>
    <div className="dropdown-menu" aria-labelledby="btnGroupDrop1">
      <Link className="dropdown-item"to="/addstudentpage" >Add Students</Link>
      <Link className="dropdown-item" to="/addrepresentativepage">Add Representative</Link>
    </div>
  </div>
  <div className="btn-group" role="group">
    <button id="btnGroupDrop1" type="button" className="btn btn-secondary dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
      View
    </button>
    <div className="dropdown-menu" aria-labelledby="btnGroupDrop1">
      <Link className="dropdown-item" to="/viewattendance">View Attendance</Link>
      <Link className="dropdown-item" to="/viewpermission">View Permissions</Link>
    </div>
  </div>
  <div className="btn-group" role="group">
    <button id="btnGroupDrop1" type="button" className="btn btn-secondary dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
      Retrieve
    </button>
    <div className="dropdown-menu" aria-labelledby="btnGroupDrop1">
      <Link className="dropdown-item" to="/Retrieveattendance">Attendance</Link>
      <Link className="dropdown-item" to="/Retrievepermission">Permissions</Link>
      <Link className="dropdown-item"to="/Retrievemonthlyattendance">Monthly Attendance</Link>
      <Link className="dropdown-item"to="/Retrievemonthlypermission">Monthly Permissions</Link>
      <Link className="dropdown-item"to="/Studentmonthlyattendance">Student Monthly Attendance</Link>
    </div>
    
  </div>


  <div className="btn-group" role="group">
    <button id="btnGroupDrop1" type="button" className="btn btn-secondary dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
      Upload
    </button>
    <div className="dropdown-menu" aria-labelledby="btnGroupDrop1">
    <Link className="dropdown-item"to="/uploadpdf">Upload Pdf</Link>
    </div>
    
  </div>


  <div className="btn-group" role="group">
    <button id="btnGroupDrop1" type="button" className="btn btn-secondary dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
      Settings
    </button>
    <div className="dropdown-menu" aria-labelledby="btnGroupDrop1">
      <Link className="dropdown-item" to="/Logout">Logout</Link>
    </div>
    
  </div>
</div></div>
}

export default Buttongroup;