import React, { useState } from 'react';
import{db} from '../files/firebase';
import{Card} from 'react-bootstrap';
import {toast} from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { useLocation } from 'react-router-dom';
toast.configure()

const Textmessages =()=>{
      var location =useLocation();  
    
    const[students,setStudents] = useState([]);
        var studentrollno = location.state.studentrollno;
        var studentsection = location.state.studentsection;
                var studentyear = location.state.studentyear;


        db.collection("studentmessages").where("rollno","==",studentrollno).where("section","==",studentsection).where("year","==",studentyear).get().then(snapshot => {
           const students = [];
           snapshot.forEach( doc => {
               const data = doc.data();
               students.push(data);
           })
          setStudents(students);
        }).catch(error => console.log(error))
    
    
   
    return <div>
        <br/><br/><br/><br/>
    <Card className="message-card">
         <h2>Messages</h2>
        <br/>
    <Card.Body>   
        {
            students.map( student => {
               
                return(
                    <div>
                        <p>Date : {student.date}</p>
                        <p>Name : {student.name}</p>
                        <p>Rollno : {student.rollno}</p>
                        <p>Section : {student.section}</p>
                        <p>Year : {student.year}</p>
                        <p>Message : {student.message}</p>     
                        <br/><br/>
                    </div>
                )
            })
        }
        
    </Card.Body>
    
    </Card>
 </div>
   
    }    

export default Textmessages;
