
import React from 'react';
import{Link} from 'react-router-dom';

class Representativeoptions extends React.Component{
  constructor(props){
    super(props);
  }
 
  state={
    studentrollno:this.props.studentrollno
  }
  render(){
     
    return <div>
         <div className="btn-group float-right" role="group" aria-label="Button group with nested dropdown"id="representative-options">
  
  <div className="btn-group" role="group">
    <button id="btnGroupDrop1" type="button" className="btn btn-secondary dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
      Settings
    </button>

    <div className="dropdown-menu" aria-labelledby="btnGroupDrop1">
      <Link className="dropdown-item" to={{pathname:"/Logout"}}>Logout</Link>
    </div>
   
  </div>
  </div>
    </div>
  }
}
export default Representativeoptions;